div.cheeseBoxBackground {
  position: relative;
  width: 100%;
  height: 120vw;
  background-image: url(./images/skos2.png);
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 160%;
}

div.cheeseBox {
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
}
div.cheeseBox img {
  width: 75%;
}

div.palmOilFree {
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  width: 15%;
}

div.ingredients img.cheeseIngredientsMainMobile {
  width: 87%;
  bottom: 7%;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: 320px) {
  div.cheeseBoxBackground {
    margin-top: 120%;
  }
}

@media (min-width: 360px) {
  div.cheeseBoxBackground {
    margin-top: 98%;
  }
}

@media (min-width: 411px) {
  div.cheeseBoxBackground {
    margin-top: 0;
  }
}

@media (min-width: 1024px) {
  div.cheeseBoxBackground {
    width: 100%;
    height: 42vw;
    max-height: 810px;
    background-image: url(./images/skos2-desktop.png);
    background-repeat: no-repeat;
    background-size: cover;
  }

  div.cheeseBoxBackground img.skos2 {
    display: none;
  }
  div.cheeseBox {
    width: 55%;
    top: -19%;
    left: 18%;
  }
}
