.cheeseInstructionTitle {
  color: #f79a1d;
  font-size: 20px;
  margin-bottom: 5px;
}

div.cheeseLine {
  text-align: center;
  background-image: url(./images/line.png);
  width: 100%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}
div.cheeseSteps div.step1,
div.cheeseSteps div.step2,
div.cheeseSteps div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #f79a1d;
}

@media (min-width: 600px) {
  .cheeseInstructionTitle {
    font-size: 35px;
  }
  div.cheeseLine {
    height: 20px;
  }
}
