div.cheeseTopBackground {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 74vh;
  min-height: 360px;
  z-index: 3;
  background-image: url(../images/background-top-cheese.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-bottom: 120%;
}

div.cheese {
  text-align: center;
  position: absolute;
  top: 10%;
  width: 100%;
}
div.cheese img {
  width: 78%;
}

p.cheeseDescription {
  position: absolute;
  top: 100%;
  color: #f79a1d;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding: 0px 40px 15px;
  margin-top: 60px;
}

div.cheesePizza {
  margin: 0 auto;
  text-align: center;
  width: 90%;
  max-width: 620px;
}

.cheesePizzaAnimation {
  margin: 0 auto;
  background-position-y: 0%;
  background-size: 99.8%;
  background-repeat: no-repeat;
  margin-left: auto;
}

@media (min-width: 320px) {
  div.cheeseTopBackground {
    margin-bottom: 90%;
  }
}

@media (min-width: 360px) {
  p.cheeseDescription {
    padding: 15px 40px;
  }
}

@media (min-width: 500px) {
  p.cheeseDescription {
    font-size: 20px;
    margin-right: 5%;
    margin-left: 5%;
    margin-top: 80px;
  }
}
@media (min-width: 550px) {
  p.cheeseDescription {
    font-size: 26px;
  }
}

@media (min-width: 760px) {
  p.cheeseDescription {
    font-size: 30px;
  }
}

@media (min-width: 1024px) {
  .cheesePizzaAnimation {
    margin: 0;
    height: 100%;
    background-position-y: 0%;
    background-size: 100%;
    background-repeat: no-repeat;
    margin-left: auto;
  }

  div.cheesePizza {
    position: absolute;
    height: auto;
    bottom: 13%;
    right: 4%;
    width: 48%;
    max-width: 1080px;
  }

  div.cheeseTopBackground {
    width: 100%;
    height: 58vw;
    max-height: 1110px;
    background-image: url(../images/skos-desktop.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 0;
  }

  div.cheese {
    text-align: left;
    top: 125px;
    margin-left: 8%;
  }
  div.cheese img {
    width: 35%;
  }
  p.cheeseDescription {
    color: #fffad4;
    top: 20%;
    left: 0;
    font-size: 16px;
    width: 38%;
  }

  div.ingredients_desktop img.desktop_cheeseIngredients {
    width: 32%;
    bottom: 8%;
    left: 6%;
  }
}
@media (min-width: 1200px) {
  p.cheeseDescription {
    font-size: 20px;
  }
}
@media (min-width: 1365px) {
  p.cheeseDescription {
    font-size: 22px;
  }
}
@media (min-width: 1439px) {
  p.cheeseDescription {
    font-size: 24px;
  }
}
@media (min-width: 1599px) {
  p.cheeseDescription {
    font-size: 25px;
  }
}
@media (min-width: 1920px) {
  p.cheeseDescription {
    font-size: 32px;
  }
}
