div.cheeseInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

img.cheeseArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arrow-mobile.png");
}

.cheeseNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.cheeseTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.cheeseLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
  margin-bottom: 15px;
}
div.cheeseLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
}

div.cheeseNutritionalValue {
  display: flex;
}

div.cheeseNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.cheeseAlergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.cheeseLineInformations {
    width: 310px;
  }
  div.cheeseLineAlergens {
    width: 140px;
  }
  p.cheeseTitle {
    font-size: 35px;
  }
  div.cheeseNutritionalValue,
  .cheeseAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.cheeseArrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.cheeseInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.cheeseTitle {
    font-size: 23px;
    color: #f79a1d;
    font-weight: 700;
  }
  div.cheeseLineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/lineYellow.png);
    background-size: contain;
    background-position: center;
  }

  div.cheeseLineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/lineYellow.png);
    background-position: center;
  }
  .cheeseNutritionalValues {
    color: #f79a1d;
    transition: 0.1s;
  }

  div.cheeseNutritionalValue,
  .cheeseAlergensDescription {
    font-size: 12px;
  }

  .cheeseAlergensDescription {
    color: #f79a1d;
    display: block;
  }
}
