@media (min-width: 1024px) {
  div.cheeseSteps_desktop {
    display: flex;
    justify-content: space-between;
  }

  h2.cheeseInstructionTitle_desktop {
    font-size: 26px;
    color: #f79a1d;
    margin-bottom: 5px;
    font-weight: 600;
    margin-top: 0;
  }
  div.cheeseLine_desktop {
    margin-top: -0.5%;
    background-image: url(../images/line.png);
    height: 15px;
    width: 260px;
    background-repeat: no-repeat;
    background-size: contain;
  }

  p.cheeseInstruction_desktop {
    margin-top: 10px;
    font-size: 16px;
    color: #f79a1d;
  }

  div.step_desktop3 p.cheeseInstruction_desktop {
    min-width: 220px;
  }
}

@media (min-width: 1280px) {
  p.cheeseInstruction_desktop {
    font-size: 18px;
  }
}

@media (min-width: 1600px) {
  h2.cheeseInstructionTitle_desktop {
    font-size: 40px;
  }
  div.cheeseLine_desktop {
    height: 23px;
    width: 400px;
  }

  p.cheeseInstruction_desktop {
    font-size: 22px;
  }
}
